
@import "kernel";

.select {
    @include component();

    .current {
        min-width: 100px;
        padding: 0 ($ui-padding + $ui-height) 0 $ui-padding;
        border-radius: 2px;
        box-shadow: 0 0 0 1px $color-border;
        height: $ui-height;
        line-height: $ui-height;
        background: $color-white;
        cursor: pointer;
        user-select: none;

        .value {
            font: {
                size: $font-size;
                family: $font-family;
            }
            height: $ui-height;
            line-height: $ui-height;

            &.default {
                color: $color-extra-light-silver !important;
            }
        }

        .arrow {
            top: 0;
            right: 0;
            width: $ui-height;
            height: $ui-height;
            position: absolute;
            display: flex;
            justify-content: center;
            border: none;
            background: none;
            align-items: center;
            outline: none;
            cursor: pointer;
            transform: rotateZ(0deg);
            transform-origin: 50%;
            z-index: 6;
            transition: transform .3s $ui-animation-swift;

            svg {
                display: block;
                fill: $color-gray;
                width: 10px;
                height: 10px;
            }
        }

        &:hover {
            box-shadow: 0 0 0 1px $color-border-hover;
            transition: box-shadow .2s $ui-animation-swift;

            .arrow {
                svg {
                    transition: fill .2s $ui-animation-swift;
                    fill: $color-light-silver;
                }

                &:active {
                    svg {
                        fill: $color-silver;
                    }
                }
            }
        }
    }

    .dropdown {
        min-width: 100px;
        background: #fff;
        position: absolute;
        width: 100%;
        border-radius: 0 0 2px 2px;
        z-index: 98;
        box-shadow:
                0 -1px 0 0 $color-white,
                0 0 0 1px $color-border-hover,
                $ui-box-shadow;
        pointer-events: none;
        opacity: 0;
        transform: translateY(-10px);
        transition:
                opacity .2s $ui-animation-swift,
                transform .2s $ui-animation-swift;

        .options {
            padding: 3px 0;
            overflow-x: visible;
            overflow-y: auto;

            &::-webkit-scrollbar {
                width: 3px;

                &-thumb {
                    border-radius: 1px;
                    background: $color-border;

                    &:hover {
                        background: $color-main;
                    }
                }
            }
        }

        .not-found {
            background: $color-white;
            padding: $ui-padding;
            text-align: center;
            overflow: hidden;
            text-overflow: ellipsis;
            color: $color-description;
        }
    }

    &.active {
        .current {
            box-shadow: 0 0 0 1px $color-border-hover;
            border-radius: 2px 2px 0 0;

            .value {
                color: $color-main;
            }

            .arrow {
                transform: rotateZ(-180deg) scale(.7);
                svg {
                    fill: $color-main;
                }
            }
        }

        .dropdown {
            pointer-events: all;
            opacity: 1;
            transform: translateY(0);
            transition:
                    opacity .3s $ui-animation-swift,
                    transform .3s $ui-animation-swift;
        }
    }

    & /deep/ .loader {
        border-color: $color-main;
        .spinner {
            box-shadow: none;
        }
    }

    &.disabled {
        .current {
            box-shadow: 0 0 0 1px $color-disabled-border;
            background: $color-disabled-bg;
            cursor: default !important;

            .value,
            .value.default {
                color: $color-disabled-body !important;
            }
            .arrow {
                cursor: default;
                svg {
                    fill: $color-disabled-body !important;
                }
            }
        }
    }

    &.searchable.active {
        .current {
            padding: 0;
            background: $color-bg !important;
            & /deep/ {
                .input {
                    display: block;
                }
                .container {
                    border-radius: 4px 4px 0 0 !important;
                    background: $color-white !important;
                    box-shadow: none !important;
                }
                input {
                    padding-right: 0 !important;
                }
            }

            .arrow svg {
                fill: $color-text !important;
            }
        }

        .dropdown {
            & /deep/ {
                .button {
                    display: block;
                    a, button {
                        border-radius: 0 0 4px 4px;
                        text-transform: none;
                        font-weight: 400;
                        box-shadow: inset 0 1px 0 $color-border;
                        letter-spacing: 0;
                        line-height: $ui-height;
                        height: $ui-height;
                    }
                }
            }
        }
    }
}



