
@import "kernel";

.password {
    @include component();

    .show-password {
        background: none;
        border: none;
        outline: none;
        display: block;
        border-radius: 50%;
        margin: 0;
        height: $ui-min-height;
        position: relative;
        cursor: pointer;
        background: rgba(#fff, .8);

        svg {
            transition: fill .2s ease;
            fill: $color-border;
            width: $ui-min-height;
            height: $ui-min-height;
            display: block;
        }

        &:after,
        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            width: 2px;
            height: 2px;
            border-radius: 50%;
            box-shadow: 0 0 0 0 rgba($color-main, 1);
            opacity: 0;
            margin: -1px 0 0 -1px;
        }

        &:after {
            transition: opacity .1s $ui-animation-swift, box-shadow .3s ease;
            opacity: 1;
            box-shadow: 0 0 1px 15px rgba($color-main, 0);
        }
    }

    &.password-visible {
        .show-password {
            svg {
                fill: $color-main !important;
            }

            &:before {
                transition: opacity .1s $ui-animation-swift, box-shadow .3s ease;
                opacity: 1;
                box-shadow: 0 0 1px 15px rgba($color-main, 0);
            }

            &:after {
                transition: none;
                box-shadow: 0 0 0 0 rgba($color-main, 1);
                opacity: 0;
            }
        }
    }
}



