
@import "kernel";

.option {
    @include component;
    margin: 2px 0;
    display: block;

    .value {
        min-width: 100px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        cursor: pointer;
        height: $ui-height - 4px;
        line-height: $ui-height - 4px;
        color: $color-text;
        padding: 0 $ui-padding;
        display: block;

        &:hover {
            background: $color-bg;
            color: $color-black;
        }
    }

    &.disabled .value {
        color: $color-border-hover;
        background: $color-white;
        cursor: default;
    }
}



