
@import "kernel";

.page-home {
    .section {
        h2, h3 {
            text-align: center;
        }

        h2 {
            font-weight: 200;
            padding: 0 0 20px 0;
        }

        p {
            font-size: 15px;
            line-height: 1.5em;
        }

        .section-body {
            max-width: $ui-width;
            margin: 0 auto;
            box-sizing: border-box;
            padding: 100px 0;
            @include clear;

            & > p {
                width: 80%;
                max-width: 600px;
            }

            p {
                margin: 0 auto;
                color: $color-silver;
                padding: 10px 0 20px 0;
                display: block;
                min-height: 60px;
            }
        }

        .section-column {
            float: left;
            width: 50%;
            display: block;
            box-sizing: border-box;
            padding: 0 50px;

            img {
                width: 80%;
                margin: 0 10%;
            }
        }
    }
}

@include media-breakpoint-down(sm) {
    .page-home {
        .section-column {
            width: 100% !important;
        }
    }
}



